<template v-if="">
  <span id="time" v-html="time"></span>
</template>

<style>
</style>

<script>
export default {
  name: "Stopwatch",
  props: ["state", "meters"],
  data: () => {
    return {
      startTime: null,
      currentTime: null,
      interval: null,
    };
  },
  created() {
    this.state = this.$props.state;
  },
  watch: {
    state: function (started) {
      if (started) {
        this.start();
      } else {
        this.stop();
      }
    },
    lastName: function (val) {
      this.fullName = this.firstName + " " + val;
    },
  },
  destroyed: function () {
    clearInterval(this.interval);
  },
  computed: {
    time: function () {
      return (
        this.hours +
        ":" +
        this.minutes +
        ":" +
        this.seconds +
        "." +
        this.decimalseconds
      );
    },
    milliseconds: function () {
      return this.currentTime - this.$data.startTime;
    },
    decimalseconds: function () {
      var lapsed = this.milliseconds;
      var dec = Math.floor((lapsed % 1000) / 10);
      return dec >= 10 ? dec : "0" + dec;
    },
    hours: function () {
      var lapsed = this.milliseconds;
      var hrs = Math.floor(lapsed / 1000 / 60 / 60);
      return hrs >= 10 ? hrs : "0" + hrs;
    },
    minutes: function () {
      var lapsed = this.milliseconds;
      var min = Math.floor((lapsed / 1000 / 60) % 60);
      return min >= 10 ? min : "0" + min;
    },
    seconds: function () {
      var lapsed = this.milliseconds;
      var sec = Math.floor((lapsed / 1000) % 60);
      return sec >= 10 ? sec : "0" + sec;
    },
  },
  methods: {
    start: function () {
      this.startTime = Date.now();
      this.currentTime = Date.now();
      this.interval = setInterval(this.updateCurrentTime, 10);
    },
    reset: function () {
      this.$data.startTime = Date.now();
      this.$data.currentTime = Date.now();
    },
    stop: function () {
      clearInterval(this.interval);
    },
    updateCurrentTime: function () {
      this.currentTime = Date.now();
    },
  },
};
</script>